/* Base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #111;
  color: #f0f0f0;
  overflow-x: hidden;
  
}

.background-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
  backdrop-filter: brightness(0.5);
}

/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 15px 5vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(10px);
  z-index: 1000;
  border-bottom: 1px solid rgba(0,255,255,0.2);
}

.nav-logo {
  font-size: 1.4rem;
  color: #00ffd5;
  font-weight: bold;
  text-shadow: 0 0 10px #00ffd5;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 25px;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  transition: 0.3s;
}

.nav-links a:hover {
  color: #00ffd5;
  text-shadow: 0 0 8px #00ffd5;
}

/* Sections */
.section {
  padding: 120px 5vw 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.content-wrapper {
  max-width: 1000px;
  width: 100%;
  text-align: center;
}

/* Home Section */
.title {
  font-size: 3rem;
  margin-left: 136px;
  color: #00ffd5;
  text-shadow: 0 0 15px rgba(0, 255, 213, 0.5);
  margin-bottom: 20px;
}

.description {
  font-size: 1.2rem;
  color: #fff;
  margin-bottom: 40px;
  text-shadow: 0 0 5px rgba(0, 255, 213, 0.5);
  margin-left: 34px;
}

/* Upload area */
.upload-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.file-upload {
  padding: 10px 25px;
  border: 2px solid #00ffff;
  border-radius: 10px;
  color: #00ffff;
  background: transparent;
  font-weight: bold;
  backdrop-filter: blur(8px);
  cursor: pointer;
  transition: 0.3s;
}

.file-upload:hover {
  background: rgba(0, 255, 255, 0.1);
  text-shadow: 0 0 10px #00ffff;
}

.generate-btn {
  padding: 12px 30px;
  font-size: 1.1rem;
  background-color: #00ffd5;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.generate-btn:hover {
  background-color: #00e6c3;
  box-shadow: 0 0 12px rgba(0, 255, 213, 0.6);
}

.generate-btn:disabled {
  background: #666;
  cursor: not-allowed;
}

.file-info {
  color: #00ffd5;
  font-size: 1rem;
}

/* Capabilities */
.capabilities {
  background: rgba(10, 10, 10, 0.85);
}

.section-title {
  font-size: 2rem;
  margin-bottom: 30px;
  color: #00ffd5;
}

.capabilities-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.capability-card {
  background: #222;
  padding: 25px;
  border-radius: 10px;
  width: 100%;
  max-width: 300px;
  box-shadow: 0 0 12px rgba(0, 255, 213, 0.2);
  transition: 0.3s;
  cursor: pointer;
}

.capability-card:hover {
  box-shadow: 0 0 18px rgba(0, 255, 213, 0.5);
  transform: translateY(-5px);
}

.capability-card h3 {
  color: #00ffd5;
  margin-bottom: 10px;
}

.footer {
  text-align: center;
  padding: 30px 20px;
  font-size: 0.9rem;
  color: #888;
  background: rgba(0, 0, 0, 0.7);
}


/* Glass container effect */
.glass-container {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  padding: 40px 30px;
  backdrop-filter: blur(12px);
  box-shadow: 0 0 25px rgba(0, 255, 213, 0.1);
  border: 1px solid rgba(0, 255, 213, 0.2);
  transition: 0.3s ease-in-out;
}



/* Home split layout */
.home-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 40px 20px;
}

/* Left side (form) */
.left-side {
  flex: 1 1 400px;
  min-width: 320px;
}

/* Right side (manga preview) */
.right-side {
  flex: 1 1 400px;
  min-width: 320px;
}

/* Manga preview box styles */
.preview-title {
  font-size: 1.5rem;
  color: #00ffd5;
  margin-bottom: 20px;
}

.preview-grid {
  display: flex;
      flex-wrap: wrap;
      gap: 15px;
      justify-content: center;
      /* background: #2d28288f; */
      position: absolute;
      background: linear-gradient(276deg, #2957a9, transparent);
      z-index: 6;
      width: 100%;
      /* backdrop-filter: brightness(0.5); */
      height: 100%;
}

.preview-box {
  background: #111;
  width: 100px;
  height: 140px;
  border: 2px solid #00ffd5;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #00ffd5;
  font-weight: bold;
  font-size: 0.9rem;
  text-align: center;
  box-shadow: 0 0 12px rgba(0, 255, 213, 0.3);
}



/* SPLIT SECTION */
.split-section {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  position: relative;
  padding: 0;
  min-height: 100vh;
  overflow: hidden;
}

