/* LEFT PANEL (GLASS SIDE) */
.left-panel {
  flex: 1 1 50%;
  padding: 60px 30px;
  backdrop-filter: blur(15px);
  background: rgba(0, 0, 0, 0.3);
  z-index: 2;
  clip-path:polygon(0 0, 100% 0, 82% 100%, 0% 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-top: solid 1px #ffffff66; */
}

.glass-content {
  max-width: 500px;
  width: 100%;
}

/* RIGHT PANEL (NORMAL SIDE) */
.right-panel {
  flex: 1 1 50%;
  border-top: solid 1px #ffffff66;
  padding: 17% 30px;
  background: url("showcase.jpeg");
  background-position: center;
  background-size: auto;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  clip-path:  polygon(25% 0, 100% 0, 100% 100%, 3% 100%);
}


/* Responsive stack */
@media (max-width: 768px) {
  .split-section {
    flex-direction: column;
  }

  .left-panel,
  .right-panel {
    clip-path: none;
    width: 100%;
    flex: unset;
  }

  .split-section::before {
    display: none;
  }
}


/* Modal backdrop */
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(5, 5, 5, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

/* Auth Modal */
.auth-modal {
  max-width: 400px;
  width: 100%;
  padding: 40px 30px;
  position: relative;
  text-align: center;
  border-radius: 15px;
  box-shadow: 0 0 25px rgba(0, 255, 213, 0.15);
}

.auth-modal h2 {
  margin-bottom: 20px;
  color: #00ffd5;
}

.auth-input {
  width: 100%;
  margin: 10px 0;
  padding: 12px;
  border: 1px solid #00ffd5;
  background: transparent;
  border-radius: 8px;
  color: white;
  font-size: 1rem;
  outline: none;
}

.auth-btn {
  width: 100%;
  margin-top: 20px;
  padding: 12px;
  font-weight: bold;
  background: #00ffd5;
  color: #000;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.auth-toggle {
  margin-top: 15px;
  font-size: 0.9rem;
  color: #00ffd5;
  cursor: pointer;
}

.auth-toggle:hover {
  text-decoration: underline;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 15px;
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #00ffd5;
  cursor: pointer;
}

/* Navbar button */
.nav-auth-btn {
  background: none;
  border: none;
  color: #00ffd5;
  font-weight: bold;
  cursor: pointer;
  font-size: 1rem;
  transition: 0.3s;
}

.nav-auth-btn:hover {
  text-shadow: 0 0 8px #00ffd5;
}
